import React from "react";
import "./Footer.style.css";
const Footer = (props) => {
  return (
    <>
      <footer id="sticky-footer" class="py-4 bg-dark text-white-50">
        <div class="container text-center">
          <small>Copyright &copy; {props.heading}</small>
        </div>
      </footer>
    </>
  );
};

export default Footer;
