import React from "react";
import Sdata from "./ServiceData";
const Cards = () => {
  return (
    <>
      <div class="row container-fluid">
        <div className="col-md-8 mx-auto">
          <h1 className="font-weight-light text-center  p-4  mb-0">
            OUR SERVICES
          </h1>
        </div>
        <div class="col-lg-11 mx-auto">
          <div class="row py-5">
            {Sdata.map((val, index) => {
              return (
                <>
                  <div class="col-lg-3">
                    <figure class="rounded p-3 bg-white shadow-sm">
                      <img src={val.imgsrc} alt="" class="w-100 card-img-top" />
                      <figcaption class="p-4 card-img-bottom">
                        <h2 class="h5 font-weight-bold mb-2 font-italic">
                          {val.title}
                        </h2>
                        <p class="mb-0 text-small text-muted font-italic">
                          {val.desc}
                        </p>
                      </figcaption>
                    </figure>
                  </div>
                </>
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
};
export default Cards;
