import React from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "./Home.comp.css";

import Cards from "./Cards.comp";
const Home = (props) => {
  return (
    <>
      <header>
        <div className="overlay"></div>
        <video
          playsinline="playsinline"
          autoplay="autoplay"
          muted="muted"
          loop="loop"
        >
          <source
            src="https://www.videvo.net/videvo_files/converted/2016_04/preview/160330_4_Typing3_Mpeg4_4K.mov82054.webm"
            type="video/mp4"
          />
        </video>
        <div className="container h-100">
          <div className="d-flex h-100 text-center align-items-center">
            <div className="w-100 text-white">
              <h1 className="display-3">{props.heading}</h1>
              <p className="lead mb-0">{props.tagline}</p>
            </div>
          </div>
        </div>
      </header>

      <section className="my-5">
        <div className="container">
          <div className="row">
            <div className="col-md-8 mx-auto">
              <h1 className="font-weight-light text-center p-4 mb-0">
                About Us
              </h1>
              <p>{props.about}</p>
            </div>
          </div>
        </div>
      </section>
      <Cards />
    </>
  );
};
export default Home;
