import React from "react";
import "./App.css";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { Route, Switch } from "react-router-dom";
import Home from "./Home.comp";
import About from "./About.comp";
import Contact from "./Contact.comp";
import Menu from "./Menu.comp";
import error404 from "./404.comp";
import Footer from "./Footer.comp";

function App() {
  const heading = "Tech Solutions";
  const tagline = "We Make IT Happen";
  const about =
    "We are a rapidly growing JavaScript & TypeScript based company. We are experts in making a single page applications. We are working on Javascript based latest frameworks like Angular, React, Vue, etc. We Have expertise in JavaScript & TypeScript. We have expertise in NodeJS with MySQL & MongoDB. Our motto is to make an application with the latest modern UI with the latest technologies. We approach agile to develop an application where we plan our sprint and execute each task one by one. We have passionate developers who develop a world-class applications with high standard coding. We will give you the quick and best solution for your business application. We always think in a creative way for application development. We build an application with DevOps CI & CD to maintain perfection on server and development. We follow a high design standard which matches modern UI. We have been the most sought-after web development company offering notable services, like the website, Web applications, Mobile applications.";
  return (
    <>
      <Menu />

      <Switch>
        <Route
          exact
          path="/"
          component={() => (
            <Home heading={heading} tagline={tagline} about={about} />
          )}
        />
        <Route exact path="/About" component={About} />
        <Route exact path="/Contact" component={Contact} />
        <Route
          component={() => (
            <Home heading={heading} tagline={tagline} about={about} />
          )}
        />
      </Switch>
      <Footer heading={heading} />
    </>
  );
}

export default App;
